import Axios from 'axios'

const api = Axios.create({
    baseURL: import.meta.env.VITE_REACT_APP_ENDPOINT_API,
    withCredentials: true,
    headers: {
        'Access-Control-Allow-Origin': 'http://localhost:3001',
        'Content-Type': 'application/json',
        Accept: 'application/json',
        Authorization: `Bearer ${localStorage.getItem('token')}`,
    },
    logging: 'error'
})

api.interceptors.request.use((config) => {
    //console.log('Request:', config.url) // Aquí puedes personalizar lo que deseas imprimir
    return config
})

api.interceptors.response.use(
    (response) => {
        //console.log('Response:', response.config.url) // Aquí puedes personalizar lo que deseas imprimir
        return response
    },
    (error) => {
        //console.error('Error:', error.config.url) // Aquí puedes personalizar lo que deseas imprimir
        return Promise.reject(error)
    }
)

export default api
